body {
  color: #212121;
  background-color: #e1f677;
  align-items: center;
  margin-left: 60px;
}

.cat-info {
  width: 600px;
  background-color: #3095dd;
  border: 1px solid #0d161b;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin-top: 20px;
  padding: 20px;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

